import { Avatar, Divider, List, ListItem, ListItemAvatar, ListItemText, Skeleton, Typography, Button, ButtonGroup, ListItemSecondaryAction } from "@mui/material";
import { useQuery } from "@tanstack/react-query";
import React from "react";
import { getShoppingListFn } from "./ShoppingListApiQueries";

function ShoppingListProductItem({ item }) {
    return (
        <ListItem alignItems="flex-start">
            <ListItemAvatar>
                <Avatar alt="Remy Sharp"/>
            </ListItemAvatar>
            <ListItemText
                primary={item.product.name}
                secondary={
                    <React.Fragment>
                        <Typography
                            sx={{ display: 'inline' }}
                            component="span"
                            variant="body2"
                            color="text.primary"
                        >

                        </Typography>
                        {item.product.brand}
                    </React.Fragment>
                }
            />
            <ListItemSecondaryAction>
                <ButtonGroup>
                    <Button>Edit</Button>
                    <Button>Delete</Button>
                </ButtonGroup>
            </ListItemSecondaryAction>
        </ListItem>
    )
}



export default function ShoppingListItemForm({ id }) {

    const { isLoading, isError, data, error } = useQuery({
        queryKey: ['shoppinglist', id],
        queryFn: async () => {
            const data = await getShoppingListFn(id)
            return data.data
        },
    })

    if (isError) {
        return <p>{JSON.stringify(error)}</p>
    }

    if (isLoading) {
        return <Skeleton />
    }



    return (
        <List sx={{ width: '100%' }}>
            {data.items.map(item => (
                <React.Fragment key={item.id}>
                    <ShoppingListProductItem item={item} />
                    <Divider variant="inset" component="li" />
                </React.Fragment>
            ))}
        </List>
    )
};

